import React from "react";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import Typography from "@material-ui/core/Typography";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import MenuBookIcon from "@material-ui/icons/MenuBook";
import LocalLibraryIcon from "@material-ui/icons/LocalLibrary";
import LibraryBooksIcon from "@material-ui/icons/LibraryBooks";
import ColorLensIcon from "@material-ui/icons/ColorLens";
import FontDownloadIcon from "@material-ui/icons/FontDownload";
import RestorePageIcon from "@material-ui/icons/RestorePage";
import ReplayIcon from "@material-ui/icons/Replay";
import GTranslateIcon from "@material-ui/icons/GTranslate";
import BookmarkBorderIcon from "@material-ui/icons/BookmarkBorder";
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import ToggleButton from "@material-ui/lab/ToggleButton";
import ToggleButtonGroup from "@material-ui/lab/ToggleButtonGroup";
import { ButtonGroup } from "@material-ui/core";
import locale from "../../locale";
import { BookType, DrawerProps } from "./types";

const Drawer = ({
  chapters,
  classes,
  handleChangeContext,
  context,
  books,
  handleChangeTheme,
  handleChangeLocale,
}: DrawerProps) => (
  <List>
    <ListItem button key="testament">
      <ListItemIcon>
        <LocalLibraryIcon />
      </ListItemIcon>
      <FormControl className={classes.formControl}>
        <InputLabel id="testament-input">
          {locale[context.state.locale].testament}
        </InputLabel>
        <Select
          labelId="testament-select-label"
          id="testament-select"
          value={context.state.testament}
          name="testament"
          onChange={handleChangeContext}
        >
          <MenuItem value={1}></MenuItem>
          <MenuItem value={2}></MenuItem>
        </Select>
      </FormControl>
    </ListItem>

    <ListItem button key="book">
      <ListItemIcon>
        <LibraryBooksIcon />
      </ListItemIcon>
      <FormControl className={classes.formControl}>
        <InputLabel id="book-input">
          {locale[context.state.locale].book}
        </InputLabel>
        <Select
          labelId="book-select-label"
          id="book-select"
          value={context.state.book}
          name="book"
          onChange={handleChangeContext}
        >
          {books &&
            books.map((row: BookType) => (
              <MenuItem value={row.id} key={row.id}>
                {row.book}
              </MenuItem>
            ))}
        </Select>
      </FormControl>
    </ListItem>

    <ListItem button key="chapter">
      <ListItemIcon>
        <MenuBookIcon />
      </ListItemIcon>
      <FormControl className={classes.formControl}>
        <InputLabel id="chapter-input">
          {locale[context.state.locale].chapter}
        </InputLabel>
        <Select
          labelId="chapter-select-label"
          id="chapter-select"
          value={context.state.chapter}
          name="chapter"
          onChange={handleChangeContext}
        >
          {chapters &&
            Array.from({ length: chapters - 1 }).map((elem, index) => (
              <MenuItem value={index + 1} key={index}>
                {index + 1}
              </MenuItem>
            ))}
        </Select>
      </FormControl>
    </ListItem>

    <ListItem key="theme">
      <ListItemIcon>
        <ColorLensIcon />
      </ListItemIcon>
      <FormControl className={classes.formControl}>
        <ButtonGroup>
          <Button
            aria-label="light"
            className={classes.buttonThemeLight}
            name="theme"
            value="0"
            onClick={handleChangeContext}
          >
            {" "}
          </Button>
          <Button
            aria-label="tan"
            className={classes.buttonThemeTan}
            name="theme"
            value="1"
            onClick={handleChangeContext}
          >
            {" "}
          </Button>
          <Button
            aria-label="dark"
            className={classes.buttonThemeDark}
            name="theme"
            value="2"
            onClick={handleChangeContext}
          >
            {" "}
          </Button>
        </ButtonGroup>
      </FormControl>
    </ListItem>

    <ListItem button key="font">
      <ListItemIcon>
        <FontDownloadIcon />
      </ListItemIcon>
      <FormControl className={classes.formControl}>
        <Grid
          container
          direction="row"
          justify="space-between"
          alignItems="center"
          // className={classes.navigation}
        >
          <Grid item>
            <Button
              variant="outlined"
              color="default"
              size="small"
              name="fontSize"
              className={classes.buttonFont}
              value={context.state.fontSize - 1}
              onClick={handleChangeContext}
            >
              -
            </Button>
          </Grid>
          <Grid item>
            <Typography>{context.state.fontSize}px</Typography>
          </Grid>
          <Grid item>
            <Button
              variant="outlined"
              color="default"
              size="small"
              name="fontSize"
              value={context.state.fontSize + 1}
              className={classes.buttonFont}
              onClick={handleChangeContext}
            >
              +
            </Button>
          </Grid>
        </Grid>
      </FormControl>
    </ListItem>

    <ListItem button key="locale">
      <ListItemIcon>
        <GTranslateIcon />
      </ListItemIcon>
      <FormControl className={classes.formControl}>
        <ToggleButtonGroup
          value={context.state.locale}
          exclusive
          aria-label="Locale"
        >
          <ToggleButton
            value="el"
            aria-label="ellinika"
            onClick={() => handleChangeLocale("el")}
          >
            <img src="assets/el.gif" alt="el" />
          </ToggleButton>
          <ToggleButton
            value="en"
            aria-label="english"
            onClick={() => handleChangeLocale("en")}
          >
            <img src="assets/en.gif" alt="el" />
          </ToggleButton>
        </ToggleButtonGroup>
      </FormControl>
    </ListItem>

    <ListItem key="bookmarks">
      <ListItemIcon>
        <BookmarkBorderIcon />
      </ListItemIcon>
      <FormControl>
        {context.state.bookmarks &&
          context.state.bookmarks.map((row: string) => (
            <Button
              key={row}
              variant="outlined"
              color="default"
              size="small"
              name="bookmark"
              // component="span"
              // className={classes.buttonFont}
              value={row}
              onClick={handleChangeContext}
            >
              {row}
            </Button>
          ))}
      </FormControl>
    </ListItem>

    <ListItem button key="reset">
      <ListItemIcon>
        <RestorePageIcon />
      </ListItemIcon>
      <FormControl className={classes.formControl}>
        <Button
          variant="outlined"
          color="default"
          value="reset"
          size="large"
          className={classes.button}
          startIcon={<ReplayIcon />}
          onClick={() => context.resetState()}
        />
      </FormControl>
    </ListItem>
  </List>
);

export default Drawer;
