import React, { useEffect, useState, useContext } from "react";
import AppBar from "@material-ui/core/AppBar";
import CssBaseline from "@material-ui/core/CssBaseline";
import Paper from "@material-ui/core/Paper";
import Drawer from "@material-ui/core/Drawer";
import Hidden from "@material-ui/core/Hidden";
import IconButton from "@material-ui/core/IconButton";
import MenuIcon from "@material-ui/icons/Menu";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import NavigateBeforeIcon from "@material-ui/icons/NavigateBefore";
import NavigateNextIcon from "@material-ui/icons/NavigateNext";
import BookmarkBorderIcon from "@material-ui/icons/BookmarkBorder";
import BookmarkIcon from "@material-ui/icons/Bookmark";
import Button from "@material-ui/core/Button";
import Accordion from "@material-ui/core/Accordion";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import AccordionDetails from "@material-ui/core/AccordionDetails";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import BorderColorIcon from "@material-ui/icons/BorderColor";
import { ThemeProvider } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import ButtonGroup from "@material-ui/core/ButtonGroup";
import AccordionActions from "@material-ui/core/AccordionActions";
import { LightTheme, DarkTheme, TanTheme } from "../../themes";
import Context from "../../components/Context";
import useStyles from "./styles";
import DrawerComponet from "./Drawer";
import { EventType, VerseType } from "./types";

const Container = () => {
  const classes = useStyles();
  const context = useContext(Context);
  let currentTheme = LightTheme;
  if (context.state.theme === 1) currentTheme = TanTheme;
  if (context.state.theme === 2) currentTheme = DarkTheme;
  const [theme, setTheme] = useState(currentTheme);
  const [verses, setVerses] = useState(null);
  const [books, setBooks] = useState(null);
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const handleChangeContext = (event: EventType) => {
    console.log(event);
    if (event.target.name === "testament") {
      context.setState({
        ...context.state,
        testament: Number(event.target.value),
        book: 1,
        chapter: 1,
      });
    } else if (event.target.name === "book") {
      context.setState({
        ...context.state,
        book: Number(event.target.value),
        chapter: 1,
      });
    } else if (event.target.name === "fontSize") {
      context.setState({
        ...context.state,
        fontSize: Number(event.target.value),
      });
    } else if (event.target.name === "bookmark") {
      const value = event.target.value.split("-");
      context.setState({
        ...context.state,
        testament: Number(value[0]),
        book: Number(value[1]),
        chapter: Number(value[2]),
      });
    } else if (event.target.name === "theme") {
      context.setState({
        ...context.state,
        theme: Number(event.target.value),
      });
    } else {
      context.setState({
        ...context.state,
        [event.target.name]: event.target.value,
      });
    }
  };

  const handleChangeTheme = () => {
    context.setState({
      ...context.state,
      theme: context.state.theme ? 0 : 1,
    });
    // setTheme(context.state.theme ? lightTheme : darkTheme);
  };

  const handleChangeLocale = (loc: "el" | "en") => {
    context.setState({
      ...context.state,
      locale: loc,
    });
  };

  const handleNavigation = (event: string) => {
    if (event === "next") {
      context.setState({
        ...context.state,
        chapter: context.state.chapter + 1,
      });
    }
  };

  const handleHighlights = (highlight: string) => {
    const { highlights } = context.state;
    const index = highlights.indexOf(highlight);
    if (index !== -1) {
      highlights.splice(index, 1);
    } else {
      highlights.push(highlight);
    }
    context.setState({
      ...context.state,
      highlights,
    });
  };

  const handleBookmark = (bookmark: string) => {
    const { bookmarks } = context.state;
    const index = bookmarks.indexOf(bookmark);
    if (index !== -1) {
      bookmarks.splice(index, 1);
    } else {
      bookmarks.push(bookmark);
    }
    context.setState({
      ...context.state,
      bookmarks,
    });
  };

  const container = window !== undefined ? () => document.body : undefined;

  const getVerses = async () => {
    const { testament, book, chapter } = context.state;
    const url = `${API_URL}/api/v1/agia-grafe/el/el_i/${testament}/${book}/${chapter}`;
    const result = await fetch(url);
    const data = await result.json();
    setVerses(data.data);
  };

  const getBooks = async () => {
    const { testament } = context.state;
    const url = `${API_URL}/api/v1/agia-grafe/books/${testament}`;
    const result = await fetch(url);
    const data = await result.json();
    setBooks(data.data);
  };

  useEffect(() => {
    getVerses();
    getBooks();
  }, [context.state]); // eslint-disable-line

  useEffect(() => {
    currentTheme = LightTheme;
    if (context.state.theme === 1) currentTheme = TanTheme;
    if (context.state.theme === 2) currentTheme = DarkTheme;
    setTheme(currentTheme);
  }, [context.state.theme]); // eslint-disable-line

  return (
    <div className={classes.root}>
      <CssBaseline />
      <ThemeProvider theme={theme}>
        <AppBar position="fixed" color="default" className={classes.appBar}>
          <Toolbar>
            <IconButton
              color="inherit"
              aria-label="open drawer"
              edge="start"
              onClick={handleDrawerToggle}
              className={classes.menuButton}
            >
              <MenuIcon />
            </IconButton>
            {verses && (
              <Typography variant="h6" noWrap>
                {verses.book} {context.state.chapter}
              </Typography>
            )}
            <div className={classes.menuButtonRight}>
              <IconButton
                aria-label="Bookmark"
                component="span"
                size="small"
                onClick={() =>
                  handleBookmark(
                    `${context.state.testament}-${context.state.book}-${context.state.chapter}`
                  )
                }
              >
                {context.state.bookmarks.includes(
                  `${context.state.testament}-${context.state.book}-${context.state.chapter}`
                ) ? (
                  <BookmarkIcon />
                ) : (
                  <BookmarkBorderIcon />
                )}
              </IconButton>
            </div>
          </Toolbar>
        </AppBar>
        <nav className={classes.drawer} aria-label="mailbox folders">
          {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
          <Hidden smUp implementation="css">
            <Drawer
              container={container}
              variant="temporary"
              anchor="left"
              open={mobileOpen}
              onClose={handleDrawerToggle}
              classes={{
                paper: classes.drawerPaper,
              }}
              ModalProps={{
                keepMounted: true, // Better open performance on mobile.
              }}
            >
              {verses &&
                DrawerComponet({
                  chapters: verses.chapters,
                  classes,
                  handleChangeContext,
                  context,
                  books,
                  handleChangeTheme,
                  handleChangeLocale,
                })}
            </Drawer>
          </Hidden>
          <Hidden xsDown implementation="css">
            <Drawer
              classes={{
                paper: classes.drawerPaper,
              }}
              variant="permanent"
              open
            >
              {verses &&
                DrawerComponet({
                  chapters: verses.chapters,
                  classes,
                  handleChangeContext,
                  context,
                  books,
                  handleChangeTheme,
                  handleChangeLocale,
                })}
            </Drawer>
          </Hidden>
        </nav>
        <Paper className={classes.content}>
          {verses &&
            verses.Chapter?.map((row: VerseType, i: number) => (
              <Accordion key={i}>
                <AccordionSummary
                  expandIcon={<ExpandMoreIcon />}
                  aria-controls={`${context.state.testament}-${context.state.book}-${context.state.chapter}-${row.verse}`}
                  id={`${context.state.testament}-${context.state.book}-${context.state.chapter}-${row.verse}`}
                  className={classes.accordionText}
                  style={{
                    fontSize: context.state.fontSize,
                    textUnderlinePosition: "under",
                    textDecorationLine: context.state.highlights.includes(
                      `${context.state.testament}-${row.book}-${row.chapter}-${row.verse}`
                    )
                      ? "underline"
                      : "none",
                  }}
                >
                  {row.text1}
                </AccordionSummary>
                <AccordionDetails style={{ fontSize: context.state.fontSize }}>
                  {row.text2}
                  <Hidden xsDown implementation="css">
                    <Button
                      endIcon={<BorderColorIcon />}
                      className={classes.buttonIcon}
                      onClick={() =>
                        handleHighlights(
                          `${context.state.testament}-${row.book}-${row.chapter}-${row.verse}`
                        )
                      }
                    />
                  </Hidden>
                </AccordionDetails>
                <Hidden smUp implementation="css">
                  <AccordionActions>
                    <ButtonGroup size="small" aria-label="actions">
                      <Button
                        endIcon={<BorderColorIcon />}
                        className={classes.buttonIcon}
                        onClick={() =>
                          handleHighlights(
                            `${context.state.testament}-${row.book}-${row.chapter}-${row.verse}`
                          )
                        }
                      />
                    </ButtonGroup>
                  </AccordionActions>
                </Hidden>
              </Accordion>
            ))}
          <Grid
            container
            direction="row"
            justify="space-between"
            alignItems="center"
            className={classes.navigation}
          >
            <Grid item>
              <Button
                variant="outlined"
                color="default"
                value="previous"
                size="large"
                disabled={
                  context.state?.book === 1 && context.state.chapter === 1
                }
                className={classes.button}
                startIcon={<NavigateBeforeIcon />}
                onClick={handleNavigation("previous")}
              />
            </Grid>
            <Grid item>
              <Button
                variant="outlined"
                color="default"
                value="next"
                size="large"
                className={classes.button}
                startIcon={<NavigateNextIcon />}
                onClick={() => handleNavigation("next")}
              />
            </Grid>
          </Grid>
        </Paper>
      </ThemeProvider>
    </div>
  );
};

export default Container;
