const GlobalValuesProvider = ({ children }: ProviderProp) => {
  const storage = {
    ...defaultState,
    ...JSON.parse(localStorage.getItem(defaultName)),
  };
  // const storage = JSON.parse(localStorage.getItem("agiaGrafe"));
  if (!storage) localStorage.setItem(defaultName, JSON.stringify(defaultState));
  // const [cache, setCache] = useState(null);
  const [state, setFinalState] = useState(storage || defaultState);

  const setState = (values: StateProp) => {
    localStorage.setItem(defaultName, JSON.stringify(values));
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
    setFinalState(values);
  };

  const resetState = () => {
    localStorage.setItem(defaultName, JSON.stringify(defaultState));
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
    setFinalState(defaultState);
  };

  // const init = async () => {
  //   const cch = await caches.open(defaultName);
  //   setCache(cch);
  // };

  // useEffect(() => {
  //   if (!cache) init();
  // }, [cache]);

  return (
    <Context.Provider value={{ state, setState, resetState }}>
      {children}
    </Context.Provider>
  );
};

export default GlobalValuesProvider;
